























	export default {
		data() {
			return {
				cardLists: []
			};
		},
		onLoad(query) {
      this.$http({
        url: this.$apis.card,
        method: 'GET',
        data: {
          recommened: query.id
        },
        cb: (err, data) => {
          if (!err && data.success) {
            this.cardLists = data.data
            this.cardLists.forEach(item => {
            	this.$set(item, 'tagArr', this.handleStr(item.show_tags))
            })
          }
          uni.setNavigationBarTitle({
          	title: query.title || '精选推荐'
          })
        }
      })
		},
    methods: {
      handleStr: function (str) {
        return str.split(',')
      },
      gotoCard(id) {
      	this.$navigateTo({
      		url: '/pages/credit/card?id=' + id
      	})
      }
    }
	}
